@import "src/scss/colors.scss";
@import "src/scss/global.scss";

// Base de la structure des boutons du design system

body .figma-btn {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  border-radius: 12px;
  //min-height: 40px;
  padding: 15px;
  cursor: pointer;
  border: 0.5px solid transparent;
  color: $noir;
  width: fit-content;
  text-decoration: none;
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
  @include media-max("sm-limit") {
    font-size: 11px;
  }

  // Ici on prcise le comportement des icnes avec svg et fill

  &.default svg {
    fill: $noir;
  }
  &:hover {
    transition: all 0.5s ease;
    svg {
      fill: $noir;
    }
  }
  &:not(.theme) {
    &:hover {
      color: $noir;
    }
    &:active {
      background: $noirCD;
      color: $noir;
    }

    // Ici on prcise le comportement du focus, il s'applique sur toutes les variantes
    &:focus {
      color: $blancSimple;
      background: $focus;
      outline: 0;
      border: 0.5px solid transparent;
      svg {
        fill: $blancSimple;
      }
    }

    // Ici on prcise le comportement "disabled"
    &:disabled,
    &.disabled {
      background: $gris;
      color: $grisFonce;
      cursor: not-allowed;
      svg {
        fill: $grisFonce;
      }
      &:hover {
        background-color: $gris;
        color: $grisFonce;
        svg {
          fill: $grisFonce;
        }
      }
    }
  }

  // Fill-Dark
  &.dark {
    background: $noir;
    color: #ffffff;
    svg {
      fill: $blancSimple;
    }
    &:hover {
      transition: all 0.5s ease;
      background: #000000;
      color: #ffffff;
      svg {
        fill: $blancSimple;
      }
    }
    &:not(:disabled):not(.disabled):active {
      background: #000000;
      color: #ffffff;
      svg {
        fill: $blancSimple;
      }
    }
  }

  // Validate

  &.validate {
    background: $vert;
    color: $blancSimple;
    svg {
      fill: $blancSimple;
    }
    &:not(:disabled):not(.disabled):hover {
      transition: all 0.5s ease;
      background: $vertFonce;
      color: $blancSimple;
      box-shadow: none;
      svg {
        fill: $blancSimple;
      }
    }
    &:not(:disabled):not(.disabled):active {
      box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.2);
      background: $vert;
    }
  }

  // Bouton d'erreur
  &.error {
    background: $error;
    color: $blancSimple;
    &:hover {
      background: $rouge;
      color: $blancSimple;
      svg {
        fill: $blancSimple;
      }
    }
    &:not(:disabled):not(.disabled):active {
      background: $error;
    }
  }

  // Fill-Light
  &.light-action {
    //min-width: 40px;
    color: $noir;
    background: $blancSimple;
    svg {
      fill: $noir;
    }
    &:not(:disabled):hover {
      background: $noir;
      box-shadow: none;
      color: $blancSimple;
      svg {
        fill: $blancSimple;
      }
    }
    &:not(:disabled):not(.disabled):active {
      background: $noir;
      color: #ffffff;
      svg {
        fill: #ffffff;
      }
    }
  }

  &.small-figma {
    padding: 0;
    width: 40px;
    height: 40px;
    margin-top: 0px;
    justify-content: center;
    align-items: center;
    background-color: #cdcdcd;
  }

  // Le bouton "merci" dans dispositif
  &.thanks {
    background: transparent;
    color: $grisFonce;
    border: 0.5px solid $grisFonce;
    padding: 15px 20px;
    &:hover {
      background: $validationDefault;
      color: $blancSimple;
      border: 0.5px solid $validationDefault;
    }
    &:active {
      background: $validationHover;
      color: $blancSimple;
      border: 0.5px solid $validationHover;
    }
    &.clicked {
      background: $validationHover;
      color: $blancSimple;
      border: 0.5px solid $validationHover;
    }
  }

  // Utilis lorsqu'un bouton utilise la couleur de thme de faon dynamique dans le JS
  &.theme {
    background: $noir;
    color: $blanc;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.2);
    svg {
      fill: $blanc;
    }
    &:hover {
      box-shadow: none;
    }
  }

  // Outline-Light
  &.outline {
    border: 0.5px solid #ffffff;
    background: transparent;
    color: #ffffff;
    transition: all 0.2s ease;
    &:hover {
      color: $noir;
      border-color: $blancSimple;
      background: $blancSimple;
      svg {
        fill: $noir;
      }
    }
  }

  // Outline-Dark
  &.outline-black {
    border: 0.5px solid $noir;
    background: transparent;
    color: $noir;
    transition: all 0.4s ease;
    svg {
      fill: $noir;
    }
    &:hover {
      border-color: $noir;
      background: $noir;
      color: $blancSimple;
      svg {
        fill: $blancSimple;
      }
    }
  }

  // La version sans texte, notamment utilise dans l'univers profil pour les tables
  &.pill {
    border-radius: 12px;
    height: auto;
    svg {
      fill: $blanc;
    }
    &:hover,
    &:not(:disabled):not(.disabled):active,
    &:focus {
      background: $blanc;
      svg {
        fill: $noir;
      }
    }
  }

  &.pill {
    border: 0.5px solid #ffffff;
    background: transparent;
    color: #ffffff;
    transition: all 0.2s ease;
    &:hover {
      border-color: #000000;
    }
    &:active {
      background: transparent;
    }
    &:focus {
      background: transparent;
    }

    &.pill-dark {
      border-color: $noir;
      color: $noir;
      svg {
        fill: $noir;
      }
      &:hover {
        background-color: #000000;
        svg {
          fill: $blanc;
        }
      }
    }
  }

  // Les gros boutons de la page d'accueil
  &.hero {
    height: 60px;
    padding: 10px 20px;
    box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.25);
  }

  // Bouton Aide ou J'ai besoin d'aide
  &.help {
    background: $erreur;
    color: $error;
    svg {
      fill: $error;
    }
    &:hover {
      background: $error;
      color: $blancSimple;
      svg {
        fill: $blancSimple;
      }
    }
    &:not(:disabled):not(.disabled):active {
      background: $erreur;
    }
  }
}
